.tab{
    width: 50%;
    
    border: 0.5px solid black;
    font-size: 5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: 'Cormorant Garamond", serif;';
    // font-weight: 500;
    cursor: pointer;

    &:hover{
        background-color: rgb(171, 171, 171);
        transition: all 0.3s ease-in;
    }
}

.tab-left{
    margin-right: 2vh ;
}

.tab-right{
    margin-left: 2vh ;
}

